function Portfolio({ reference, fixedFrameHideFun, activePanelSet, activePanel, topOffsetFixedPanel }) {

    const panelRef = useRef()
    const [activeStage, setActiveStage] = useState(0)
    const [scrollPos, setScrollPos] = useState(0)

    useEffect(() => {
        topOffsetFixedPanel(panelRef.current.getBoundingClientRect().top)

        reference.current.addEventListener('scroll', function() { 
            setScrollPos(reference.current.scrollTop)
            if (reference.current.scrollTop > window.innerHeight) {        
                if (document.getElementById('panel1').offsetTop >= 0) {
                document.getElementById('panel1').style.transform = 'translateY(0px)'
                document.getElementById('panel2').style.transform = 'translateY(0px)'    
                document.getElementById('panel3').style.transform = 'translateY(0px)'
                document.getElementById('panel4').style.transform = 'translateY(0px)'
                document.getElementById('panel5').style.transform = 'translateY(0px)'
                document.getElementById('number1').style.opacity = "0"
                document.getElementById('number2').style.opacity = "0"
                document.getElementById('number3').style.opacity = "0"
                document.getElementById('number4').style.opacity = "0"
                document.getElementById('number5').style.opacity = "0"
                }
            } else {
                if (document.getElementById('panel1').offsetTop <= 150) {
                    document.getElementById('panel1').style.transform = 'translateY(150px)'
                    document.getElementById('panel2').style.transform = 'translateY(150px)'
                    document.getElementById('panel3').style.transform = 'translateY(150px)'
                    document.getElementById('panel4').style.transform = 'translateY(150px)'
                    document.getElementById('panel5').style.transform = 'translateY(150px)'
                    document.getElementById('number1').style.opacity = "1"
                    document.getElementById('number2').style.opacity = "1"
                    document.getElementById('number3').style.opacity = "1"
                    document.getElementById('number4').style.opacity = "1"
                    document.getElementById('number5').style.opacity = "1"
                }
            }
            })

    }, [])

    const panelClickHandler = (data) => {   
        if (scrollPos < window.innerHeight/1.5) {
            reference.current.scrollTo(0, window.innerHeight-20)
            return
        }
        fixedFrameHideFun(true)
        activePanelSet(data.id)
    }


  return (
        <div className='portfolioContainer' id="portfolioContainer" >
            {
                categories.map(data => {
                    return (
                        <div ref={panelRef} key={data.id} onClick={() => {
                            panelClickHandler(data)
                          }}
                          className={"panel panel"+data.id} id={"panel"+data.id} style={activePanel === data.id ? {flex: 18} : {flex: 2}}>
                        
                        {(activePanel === data.id) ? 
                        <div className='activePanel'></div>
                        : <div className='fadedImage'></div> }

                        {activePanel === data.id ? null : 
                        <div id="panelTitle" style={activePanel !== 0 && activePanel !== data.id ? { writingMode:'vertical-rl', transform: 'rotate(180deg)', textAlign: 'center', display: 'flex', justifyContent: 'center', height: '100%', alignItems: 'center', marginLeft: 0} : {}} className='panelTitle'>
                            {data.title}
                        </div>}

                        {activePanel === 0 ? (<>
                        <span className='panelDesc'>{data.description}</span>
                        <div className='viewBtn'>
                            <span>View Projects</span>
                            <img src={Arrow} alt="arrow"/>
                        </div>
                        </>
                        ) : 
                        (activePanel === data.id) ? (
                            <div className='activePanelItem'>
                                <div className='stageContainer'>
                                    <div className='stageHeader'>
                                        <div className='stageHeaderItem' onClick={() => setActiveStage(0)}>
                                            <span>Stage1</span>
                                            <img src={Arrow} />
                                        </div>
                                        <div className='stageHeaderItem' onClick={() => setActiveStage(1)}>
                                            <span>Stage2</span>
                                            <img src={Arrow} />
                                        </div>
                                        <div className='stageHeaderItem' onClick={() => setActiveStage(2)}>
                                            <span>Stage3</span>
                                        </div>
                                    </div>

                                    <div className='stageDesc'>
                                        <p>{data.stages[parseInt(activeStage)]}</p>
                                    </div>
                                </div>

                                <div className='activeDesc'>
                                    <div className='activeDescTitle'>{data.title}</div>
                                    <div className='activeDescDescription'>{data.description}</div>
                                </div>
                            </div>
                        ) : null}
                    </div>
                    )       
                })
            }
    )    
    </div>
  )
}

export default Portfolio